/* gray_bg_tab */
.hs-gray-bg-tab { background: #f1f1f1; font-size: 0; }

.hs-gray-bg-tab.size01 li { width: 100%; }

.hs-gray-bg-tab.size02 li { width: 50%; }

.hs-gray-bg-tab.size03 li { width: 33.33333%; }

.hs-gray-bg-tab.size04 li { width: 25%; }

.hs-gray-bg-tab.size05 li { width: 20%; }

.hs-gray-bg-tab.size06 li { width: 16.66667%; }

.hs-gray-bg-tab li { display: inline-block; position: relative; }

.hs-gray-bg-tab li::before { background: #cecece; clear: both; content: ""; display: inline-block; height: 12px; margin-top: -6px; width: 1px; left: 0; position: absolute; top: 50%; }

.hs-gray-bg-tab li:first-child::before { display: none; }

.hs-gray-bg-tab li a { color: #888; display: block; font-size: 14px; padding: 14px 0 13px; text-align: center; }

.hs-gray-bg-tab li.on a { color: #222; font-weight: bold; }

/* red_border_tab */
.hs-red-border-tab { background: #fff; font-size: 0; position: relative; }

.hs-red-border-tab.size01 li, .hs-red-border-tab .fcs { width: 100%; }

.hs-red-border-tab.size02 li, .hs-red-border-tab .fcs { width: 50%; }

.hs-red-border-tab.size03 li, .hs-red-border-tab .fcs { width: 33.33333%; }

.hs-red-border-tab.size04 li, .hs-red-border-tab .fcs { width: 25%; }

.hs-red-border-tab.size05 li, .hs-red-border-tab .fcs { width: 20%; }

.hs-red-border-tab.size06 li, .hs-red-border-tab .fcs { width: 16.66667%; }

.hs-red-border-tab li { display: inline-block; }

.hs-red-border-tab li a { border-bottom: 2px solid #f3f3f3; color: #aaa; display: block; font-size: 14px; font-weight: bold; line-height: 1; padding: 14px 0 15px; text-align: center; }

.hs-red-border-tab li a .s-txt { display: inline-block; font-size: 12px; margin-left: 5px; }

.hs-red-border-tab li.on a { border-color: #e46764; color: #222; }

.hs-red-border-tab.ui-tab li.on a { border-color: transparent; }

.hs-red-border-tab.ui-tab li.on:first-child a { border-color: #e46764; }

.hs-red-border-tab.ui-tab.active li.on:first-child a { border-color: transparent; }

.hs-red-border-tab.ui-tab .fcs { background-color: #c69c6d; height: 2px; bottom: 0; left: 0; position: absolute; -webkit-transform: translateX(0); -khtml-transform: translateX(0); -moz-transform: translateX(0); -ms-transform: translateX(0); -o-transform: translateX(0); transform: translateX(0); -webkit-transition: all 0.4s ease-in-out; -khtml-transition: all 0.4s ease-in-out; -moz-transition: all 0.4s ease-in-out; -ms-transition: all 0.4s ease-in-out; -o-transition: all 0.4s ease-in-out; transition: all 0.4s ease-in-out; }

